$bgcolor: #202023e5;
$fgcolor: #e2e2e2;
$actcolor: #60a5fa;

.SettingsWindow {
  img {
    display: block;
  }

  .Window .TopBar {
    padding: 4px 8px;
    .TopBarInteractionContainer {
      display: flex;
      width: 100vw;
      justify-content: space-between;
      align-items: center;
    }
    .TopBarTitle {
      width: 150px;
    }
  }

  .settings {
    width: 93vw;
    height: 93%;
    opacity: 0;
    pointer-events: none;
    transition: all ease 0.3s;

    &.active {
      opacity: 1;
      pointer-events: auto;
      width: 100vw;
      height: 100%;
    }

    &.hide {
      top: 153%;
      pointer-events: none;
    }

    .BlackScr {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      background: transparentize($bgcolor, 0.1);
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all ease 0.3s;
      opacity: 0;
      pointer-events: none;

      &.active {
        opacity: 1;
        pointer-events: auto;
      }

      .WifiSharing {
        position: absolute;
        display: flex;
        flex-direction: column;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 5px;
        overflow: hidden;
        z-index: inherit;
        opacity: 0;
        pointer-events: none;
        width: 400px;
        box-shadow: 0px 0 20px #00000089;

        &.active {
          opacity: 1;
          pointer-events: auto;
        }

        .WindowTopBar {
          background: $bgcolor;
          backdrop-filter: blur(15px);
          -webkit-backdrop-filter: blur(15px);
          padding: 7px 10px;
          display: flex;
          align-items: center;
          transition: all ease 0.3s;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;

          .WindowTopBarTitle {
            color: $fgcolor;
            font-size: 12px;
            margin: 0;
            width: 100%;
          }

          .WindowTopBarInteractionContainer {
            display: flex;

            .WindowTopBarInteraction {
              display: flex;
              justify-content: center;
              align-items: center;
              margin-left: 7px;
              border-radius: 9999px;
              width: 20px;
              height: 20px;
              font-size: 11px;
              color: $fgcolor;

              &:hover {
                background: #3b3b3f;
              }

              &.close:hover {
                background: #ff5252;
              }
            }
          }
        }

        .WindowBodyDefault {
          background: $bgcolor;
          backdrop-filter: blur(15px);
          -webkit-backdrop-filter: blur(15px);
          border-bottom-left-radius: 5px;
          border-bottom-right-radius: 5px;
          padding: 25px 15px;
          color: $fgcolor;
          display: flex;
          justify-content: center;
          align-items: center;
          text-align: center;

          * {
            margin: 0;
          }

          .WindowBodyTitle {
            font-size: 17px;
            margin-bottom: 10px;
            font-weight: 700;
          }

          .WindowBodyContent {
            font-size: 35px;
          }
        }

        &.whitesur {
          .WindowTopBar {
            background: #202023;
            padding-left: 12px;
            padding-right: 12px;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            border: 0.5px solid lighten(#202023, 25%);
            border-bottom: none;

            .WindowTopBarInteractionContainer {
              .WindowTopBarInteraction {
                width: 13px;
                height: 13px;
                color: $bgcolor;
                font-size: 8px;
                margin-left: 8px;

                i {
                  display: none;
                }

                &.close {
                  background: #ff5e57;

                  &:active {
                    background: lighten(#ff5e57, 20%);
                  }

                  &:hover {
                    i {
                      display: block;
                    }
                  }
                }
              }
            }
          }
          .WindowBodyDefault {
            background: #202023;
            border: 0.5px solid lighten(#202023, 25%);
            border-top: none;
          }
        }
      }

      .UsersBox {
        position: absolute;
        display: flex;
        flex-direction: column;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
