          }

          .WindowBodyContent {
            padding: 0 30px;
            display: flex;
            align-items: center;
            flex-direction: column;

            .UserInfo {
              margin-top: 40px;
              margin-bottom: 20px;

              .Input {
                position: relative;
                display: flex;
                justify-content: space-between;
                align-items: center;
                appearance: none;
                border: none;
                outline: none;
                color: $fgcolor;
                border-radius: 5px;
                width: 350px;
                font-size: 13px;
                padding: 7px 12px;
                margin-bottom: 10px;
                background: transparentize($fgcolor, 0.93);
                transition: all ease 0.3s;

                &::-ms-clear,
                &::-ms-reveal {
                  display: none;
                }

                &:focus {
                  border-bottom: 1px solid $actcolor;
                }
              }
            }

            .Button {
              border-radius: 999px;
              background: transparentize($fgcolor, 0.93);
              padding: 10px 30px;
              font-size: 13px;
              transition: all ease 0.25s;

              &.disabled {
                opacity: 0.4;
                pointer-events: none;
              }

              &:active {
                background: transparentize($fgcolor, 0.89);
                transition: none;
              }
            }
          }
        }

        &.whitesur {
          .WindowTopBar {
            background: #202023;
            padding-left: 12px;
            padding-right: 12px;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            border: 0.5px solid lighten(#202023, 25%);
            border-bottom: none;

            .WindowTopBarInteractionContainer {
              .WindowTopBarInteraction {
                width: 13px;
                height: 13px;
                color: $bgcolor;
                font-size: 8px;
                margin-left: 8px;

                i {
                  display: none;
                }

                &.close {
                  background: #ff5e57;

                  &:active {
                    background: lighten(#ff5e57, 20%);
                  }

                  &:hover {
                    i {
                      display: block;
                    }
                  }
                }
              }
            }
          }
          .WindowBodyDefault {
            background: #202023;
            border: 0.5px solid lighten(#202023, 25%);
            border-top: none;
          }
        }
      }

      .InsertWifiPassword {
        position: absolute;
        display: flex;
        flex-direction: column;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 5px;
        overflow: hidden;
        z-index: inherit;
        opacity: 0;
        pointer-events: none;
        width: 450px;
        box-shadow: 0px 0 20px #00000089;

        &.active {
          opacity: 1;
          pointer-events: auto;
        }

        .WindowBodyDefault {
          background: $bgcolor;
          backdrop-filter: blur(15px);
          -webkit-backdrop-filter: blur(15px);
          border-radius: 5px;
          padding: 10px 15px;
          padding-top: 30px;
          color: $fgcolor;

          * {
            margin: 0;
          }

          .WindowBodyTitle {
            font-size: 17px;
            margin-bottom: 10px;
            font-weight: 700;
          }

          .WindowBodyContent {
            display: flex;

            .WindowBodyIcon {
              font-size: 18px;
            }

            .PasswordContainer {
              display: flex;
              margin-top: 8px;

              &.disabled {
                .InputPassword {
                  background: transparentize($fgcolor, 0.98);
                  color: transparentize($fgcolor, 0.7);
                }
                * {
                  color: transparentize($fgcolor, 0.7);
                  pointer-events: none;
                }
              }

              .InputPassword {
                position: relative;
                appearance: none;
                border: none;
                outline: none;
                color: $fgcolor;
                border-radius: 5px;
                width: 100%;
                font-size: 12px;
                padding: 5px 10px;
                background: transparentize($fgcolor, 0.93);
                transition: all ease 0.1s;

                &::-ms-clear,
                &::-ms-reveal {
                  display: none;
                }

                &:focus {
                  border-bottom: 1px solid $actcolor;
                }

                &.wrongPassword {
                  border-bottom: 1px solid #ff9191;

                  &.activeAnimation {
                    animation: wrongPassword 0.5s !important;

                    @keyframes wrongPassword {
                      0% {
                        right: 0;
                      }
                      20% {
                        right: 5px;
                      }
                      40% {
                        right: -5px;
                      }
                      60% {
                        right: 5px;
                      }
                      80% {
                        right: -5px;
                      }
                      100% {
                        right: 0px;
                      }
                    }
                  }
                }
              }

              .displayPassword {
                margin-left: 5px;
                width: 30px;
                height: 27px;
                border-radius: 999px;
                font-size: 13px;
                display: flex;
                justify-content: center;
                align-items: center;

                &.disabled {
                  opacity: 0;
                  pointer-events: none;
                }

                &:hover {
                  background: transparentize($fgcolor, 0.9);
                }
              }
            }
          }
        }

        .WindowBodyButton {
          display: flex;
          flex-direction: row-reverse;
          margin: 15px 0;
          margin-bottom: 0;

          .Button {
            border: none;
            outline: none;
            background: #ffffff10;
            color: $fgcolor;
            font-size: 12px;
            padding: 7px 20px;
            border-radius: 5px;
            margin-left: 5px;

            &:hover {
              background: #ffffff20;
            }

            &.disabled {
              background: transparentize($fgcolor, 0.98);
              color: transparentize($fgcolor, 0.7);
              pointer-events: none;
            }
          }
        }

        &.whitesur {
          border-radius: 10px;
          .WindowBodyDefault {
            border-radius: 10px;
            background: #202023;
            border: 0.5px solid lighten($bgcolor, 25%);

            .WindowBodyContent {
              .PasswordContainer {
                .InputPassword {
                  &:focus {
                    border: 3px solid transparentize(#009dff, 0.6);
                  }

                  &.wrongPassword {
                    border: 3px solid transparentize(#ff9191, 0.6);
                  }
                }
              }
            }
          }
        }
      }

