  .Settings {
    background: $bgcolor;
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    height: 100%;
    color: $fgcolor;

    .SettingsSection {
      width: 100%;
      height: 100%;
      display: flex;

      div {
        .NavPanel {
          background: transparentize($actcolor, 0.95);
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          padding: 10px 0;
          margin: 7px;
          border-radius: 13px;

          .DropdownMenu {
            display: flex;
            align-items: center;
            color: darken($fgcolor, 10%);
            margin: 7px 0;
            margin-left: 20px;
            font-size: 13px;
            transition: all ease 0.3s;

            i {
              width: 10px;
              display: flex;
              justify-content: center;
            }

            p {
              margin: 0;
              padding: 8px 0 8px 12px;
              border-radius: 999px;
              border-top-right-radius: 0;
              border-bottom-right-radius: 0;
              width: 100%;
            }

            &:hover {
              color: $actcolor;
            }

            &.active {
              color: $actcolor;
              margin-left: 10px;

              i {
                width: 30px;
              }

              p {
                font-weight: 700;
                background: transparentize($actcolor, 0.87);
              }
            }
          }
        }
      }

      .SettingsContainer {
        position: relative;
        width: 100%;
        height: 100%;

        .SettingsContainerInside {
          position: absolute;
          width: 100%;
          height: 100%;
          overflow-y: auto;
          padding: 20px;
          font-size: 13px;

          p {
            margin: 0;
          }

          .MenuSection {
            font-size: 12px;
            padding: 7px 12px;
            background: transparentize($fgcolor, 0.95);
            display: flex;
            justify-content: space-between;
            width: 199px;
            align-items: center;
            border-radius: 7px;
          }

          .WiFiWrapper {
            .WiFi {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              height: 100%;

              .StatusWifiFalse {
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                text-align: center;

                i {
                  font-size: 70px;
                  margin-bottom: 40px;
                  color: transparentize($fgcolor, 0.8);
                }

                .Title {
                  font-size: 40px;
                  margin-bottom: 10px;
                }

                .Description {
                  color: transparentize($fgcolor, 0.8);
                }
              }

              .VisibleNetworks {
                display: flex;
                flex-direction: column;
                height: 100%;

                .VisibleNetworksItem {
                  width: 100%;
                  padding: 15px 25px;
                  display: flex;
                  justify-content: space-between;
                  border-radius: 999px;
                  margin-bottom: 7px;

                  &:hover {
                    background: transparentize($fgcolor, 0.95);
                  }

                  .VisibleNetworksIcon {
                    i {
                      margin-right: 13px;

                      &:last-child {
                        margin: 0;
                      }

                      &.fa-duotone {
                        --fa-secondary-color: #e2e2e296;
                      }
                    }
                  }
                }
              }
            }
          }

          .BluetoothWrapper {
            height: 100%;
            .Bluetooth {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              height: 100%;

              .BluetoothDevices {
                .Description {
                  color: transparentize($fgcolor, 0.8);
                }
              }

              .StatusBluetoothFalse {
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                text-align: center;

                i {
                  font-size: 70px;
                  margin-bottom: 40px;
                  color: transparentize($fgcolor, 0.8);
                }

                .Title {
                  font-size: 40px;
                  margin-bottom: 10px;
                }

                .Description {
                  color: transparentize($fgcolor, 0.8);
                }
              }
            }
          }

          .AppearanceWrapper {
            .Appearance {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              margin-bottom: 30px;
              max-width: 70%;
              .WindowColors {
                display: flex;
                flex-direction: column;

                .ImageContainer {
                  border: 3px solid transparent;
                  border-radius: 10px;
                  img {
                    border-radius: 8px;
                    width: auto;
                    height: 200px;
                  }
                }

                &[value="1"] {
                  .ImageContainer.dark {
                    transition: border ease 0.3s;
                    border: 3px solid #60a5fa;
                  }
                }

                &[value="2"] {
                  .ImageContainer.light {
                    transition: border ease 0.3s;
                    border: 3px solid #60a5fa;
                  }
                }
              }

              .Wallpapers {
                display: flex;
                flex-direction: column;

                .WImageContainer {
                  transition: border ease 0.3s;
                  border: 3px solid transparent;
                  border-radius: 10px;
                  img {
                    border-radius: 8px;
                    width: auto;
                    height: 118px;
                  }
                }

                &[value="1"] {
                  .WImageContainer.w1 {
                    border: 3px solid #60a5fa;
                  }
                }

                &[value="2"] {
                  .WImageContainer.w2 {
                    border: 3px solid #60a5fa;
                  }
                }

                &[value="3"] {
                  .WImageContainer.w3 {
                    border: 3px solid #60a5fa;
                  }
                }

                &[value="4"] {
                  .WImageContainer.w4 {
                    border: 3px solid #60a5fa;
                  }
                }

                &[value="5"] {
                  .WImageContainer.w5 {
                    border: 3px solid #60a5fa;
                  }
                }

                &[value="6"] {
                  .WImageContainer.w6 {
                    border: 3px solid #60a5fa;
                  }
                }

                .AddWallpaper {
                  border-radius: 15px;
                  border: 4px dashed transparentize($fgcolor, 0.95);
                  color: transparentize($fgcolor, 0.85);
                  background: transparentize($fgcolor, 0.95);
                  width: 649.52px;
                  height: 189px;
                  font-size: 20px;
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  width: 100%;
                }

                .WallpaperBg {
                  width: 649.52px;
                  height: 189px;
                  position: relative;

                  .CloseButton {
                    border: none;
                    outline: none;
                    appearance: none;
                    position: absolute;
                    top: -10px;
                    right: -10px;
                    background: $fgcolor;
                    color: $bgcolor;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 27px;
                    height: 27px;
                    font-size: 14px;
                    border-radius: 999px;
                    transition: all ease 0.3s;

                    &:active {
                      background: darken($fgcolor, 15%);
                      transition: none;
                    }
                  }

                  .WallpaperImageWrapper {
                    width: 100%;
                    height: 100%;
                    overflow: hidden;
                    border-radius: 15px;

                    .WallpaperImage {
                      width: 100%;
                      height: 100%;
                      background-position: center;
                      background-size: cover;
                    }
                  }
                }

                .AddWallpaperInput {
                  position: absolute;
                  top: 0;
                  left: 0;
                  bottom: 0;
                  right: 0;
                  width: 100%;
                  opacity: 0;
                  border-radius: 15px;
                }
              }

              .HeaderAppearance {
                display: flex;
                flex-direction: column;
              }

              .Themes {
                display: flex;
                flex-direction: column;

                .ThemesMenu {
                  display: flex;
                  justify-content: space-between;
                  margin-bottom: 10px;

                  &:last-child {
                    margin: 0;
                  }
                }
              }
            }
          }

          .WidgetsWrapper {
            .Widgets {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              margin-bottom: 30px;
              max-width: 70%;

              .CurrentWidgets {
                display: flex;
                flex-direction: column;

                .WidgetsButton {
                  height: 20px;
                  width: 20px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  transition: all ease 0.3s;
                  border-radius: 999px;
                  &:hover {
                    background: transparentize($fgcolor, 0.8);
                  }
                }

                .WidgetsContainer {
                  display: flex;

                  & > * {
                    margin-right: 20px;
                  }

                  .ClockWidget {
                    position: relative;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                  }
                }
              }
            }
          }

          .NotificationsWrapper {
            .Notifications {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              height: 100%;
            }
          }

          .DisplaysWrapper {
            .Displays {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              margin-bottom: 30px;
              max-width: 70%;
              .BuiltInDisplay {
                display: flex;
                flex-direction: column;
              }

              .BiDMenu {
                display: flex;
                justify-content: space-between;
                margin-bottom: 10px;

                &:last-child {
                  margin: 0;
                }
              }
            }
          }

          .UsersWrapper {
            margin: 10px 0;
            .Users {
              display: flex;
              justify-content: center;

              .UserCard {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 40px;

                .UserInfo {
                  display: flex;
                  align-items: center;

                  .UserName {
                    margin-bottom: 5px;
                    font-size: 30px;
                    font-weight: 700;
                  }

                  .UserRole {
                    text-transform: capitalize;
                    font-size: 13px;
                    color: transparentize($fgcolor, 0.8);
                  }
                }
              }

              .UserItem {
                width: 100%;
                padding: 17px 25px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-radius: 999px;
                margin-bottom: 7px;

                .UserIcon {
                  margin-right: 10px;
                }

                &:hover {
                  background: transparentize($fgcolor, 0.95);
                }
              }
            }
          }

          .RegionNLanguageWrapper {
            .RegionNLanguage {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              height: 100%;
            }
          }

          .DateNTimeWrapper {
            .DateNTime {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              height: 100%;
            }
          }

          .AboutWrapper {
            .About {
              display: flex;
              justify-content: center;
              margin: 0 auto;
              width: 70%;

              .AboutLogo {
                margin: 0 auto;
              }

              .AboutMenu {
                width: 600px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 15px 25px;
                background: transparentize($fgcolor, 0.93);
                border-bottom: 0.5px solid darken($fgcolor, 73%);

                &:hover {
                  background: transparentize($fgcolor, 0.9);
                }

                &:first-child {
                  border-top-left-radius: 7px;
                  border-top-right-radius: 7px;
                }

                &:last-child {
                  border: 0;
                  border-bottom-left-radius: 7px;
                  border-bottom-right-radius: 7px;
                }

                &.EditName {
                  padding: 0 15px;
                  display: flex;
                  height: 0;
                  transition: all ease 0.3s;
                  border: 0 !important;

                  &:hover {
                    background: transparentize($fgcolor, 0.93);
                  }

                  * {
                    transition: all ease 0.3s;
                  }

                  .EditNameInput {
                    appearance: none;
                    border: none;
                    outline: none;
                    background: transparentize($fgcolor, 0.9);
                    color: inherit;
                    width: 100%;
                    height: 0;
                    padding: 0;
                    font-size: 11px;
                  }

                  &.active {
                    padding: 8px 15px;
                    height: 44.5px;

                    .EditNameInput {
                      padding: 7px 15px;
                      height: 28px;
                      border-radius: 5px;

                      &:focus {
                        border-bottom: 1px solid $actcolor;
                      }
                    }
                  }
                }

                .BlurText {
                  color: transparentize($fgcolor, 0.8);
                }

                i {
                  transition: all ease 0.3s;

                  &.rotated {
                    transform: rotate(90deg);
                  }
                }
              }
            }
          }
        }
      }
    }

    &.whitesur {
      background: #202023;

      .SettingsSection {
        div {
          .NavPanel {
            border-radius: 0;
            background: transparent;
            border-bottom: 0.5px solid transparentize($fgcolor, 0.8);

            &:last-child {
              border-bottom: none;
            }

            .DropdownMenu {
              transition: none;
              margin-left: 15px;
              i {
                color: #009dff;
                font-size: 15px;
                width: 15px;
                margin-right: 7px;
              }
              p {
                padding: 7px 8px;
                color: darken($fgcolor, 10%);
                border-radius: 5px;
                font-weight: 300;
              }
              &:hover {
                p {
                  background: transparentize($fgcolor, 0.93);
                }
              }

              &.active {
                margin-left: 15px;
                i {
                  width: 15px;
                  margin-right: 7px;
                }

                p {
                  font-weight: inherit;
                  background: transparentize($fgcolor, 0.85);
                }

                &:hover {
                  p {
                    background: transparentize($fgcolor, 0.85);
                  }
                }
              }
            }
          }
        }

        .SettingsContainer {
          .SettingsContainerInside {
            .WiFiWrapper {
              .WiFi {
                .VisibleNetworks {
                  .VisibleNetworksItem {
                    border-radius: 5px;
                    padding: 8px 20px;

                    &:hover {
                      background: #007bff;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

